#oopss {
    background: linear-gradient(-45deg, #f1f1f1, #fafafa);
    position: fixed;
    left: 0px;
    top: 0;
    width: 100%;
    height: 100%;
    line-height: 1.5em;
    z-index: 9999;
  }
  #oopss #error-text {
    font-size: 40px;
    display: flex;
    flex-direction: column;
    align-items: center;
    font-family: "Shabnam", Tahoma, sans-serif;
    color: #111111;
    
  }
  #oopss #error-text img {
    margin: 85px auto 20px;
    height: 342px;
  }
  #oopss #error-text span {
    position: relative;
    font-size: 3.3em;
    font-weight: 900;
    margin-bottom: 50px;
    color: #111111;
  }

  #oopss #error-text p.p-a {
    font-size: 19px;
    margin: 30px 0 15px 0;
    padding: 0 300px 0px;
    text-align: center;
    color: #111111;
    
  }
  #oopss #error-text p.p-b {
    font-size: 15px;
    color: #111111;
  }

  #oopss #error-text p.p-c {
    font-size: 19px;
    margin: 30px 0 15px 0;
    padding: 0 300px 0px;
    text-align: center;
    color: #06c56d;
    font-weight: 600;
  }